.form {
    &__input {
        width: 100%;
        height: 60px;
        &:not(:first-child) {
            margin-top: 24px;
        }
        &_label {
            height: 22px;
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 155%;
            color: #ffffff;
        }
        &_field {
            width: 100%;
            height: 38px;
            background: #1d283a;
            border: none;
            border-bottom: 1px solid #fff;
            padding: 0;

            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 155%;
            color: #ffffff;
            &::placeholder {
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 155%;
                color: #ffffff;
                opacity: 0.7;
            }
            &:focus {
                outline: none;
            }
            // &_error {
            //     color: red;
            //     border-color: red;
            //     width: 306px;
            // }
        }
    }
}

.password {
    display: flex;
    align-items: flex-end;
    &__decor {
        position: relative;
        width: 24px;
        height: 30px;
        border-bottom: 1px solid #fff;
        cursor: pointer;
        &_eye {
            position: absolute;
            top: 4px;
            width: 24px;
            height: 22px;
            background-image: url('../../assets/Eye.svg');
            background-repeat: no-repeat;
        }
        &_line {
            position: absolute;
            top: 0;
            width: 22px;
            height: 22px;
            background-image: url('../../assets/Line.svg');
            // display: none;
        }
    }
}

.error {
    color: red;
    &__message {
        color: red;
        font-size: 10px;
    }
    &__input {
        color: red;
        border-color: red;
        width: 306px;
    }
    &__decor {
        border-color: red;
    }
}

.visibility {
    display: none;
}
